import {notifyAnnounceView} from '@/api/basic';
import moment from '@/common/moment';

export default {
	data() {
		return {
			announceDetail: {},
			id: undefined,
			loading: true
		}
	},
	filters: {
		// 时间格式化
		time(val) {
			return moment(val * 1000).format('YYYY-MM-DD HH:mm')
		}
	},
	onLoad(event) {
		this.id = event.id;
		this.getNotifyAnnounceView(event.id);
	},
	onShareAppMessage() {
		return {
			title: this.banner.title,
			path: `/pages/index/notice/detail?id=${this.id}`
		}
	},
	methods: {
		// 获取通知详情
		async getNotifyAnnounceView(id) {
			await this.$http.post(`${notifyAnnounceView}`, {
				keyName : id
				}).then(r => {
				this.loading = false;
				this.announceDetail = r.data;
				uni.setNavigationBarTitle({
					title: r.data.title
				});
			}).catch(() => {
				this.loading = false;
			})
		}
	}
}
